import React from 'react';

import "./App.css";

import Monthly from "./components/Monthly";

import Annual from "./components/Annual"

class App extends React.Component{
  constructor(props){
    super(props);
    this.state={
      input_electricity:'',
      input_phone:'',
      input_water:'',
      input_gas:'',
      input_homeTax:'',
      output_electricity:'',
      output_phone:'',
      output_water:'',
      output_gas:'',
      output_homeTax:'',
      monthly_bill:'',
      annual_bill: ''
    }
    // 
    this.handleChange = this.handleChange.bind(this);
    this.calculateItem = this.calculateItem.bind(this);
  }

  handleChange = ({ target }) => {
    // method to handle input for each input blocks in form
    this.setState({ [target.name]: target.value });
 };

 calculateItem = (e) =>{
  //  To calculate total expenses
    e.preventDefault();
    const electricity = Number(this.state.input_electricity)*5;
    const phone = Number(this.state.input_phone)/100;
    const water = Number(this.state.input_water)/1000;
    const gas = Number(this.state.input_gas)*55;
    const homeTax = Number(this.state.input_homeTax)*10;
    const total = electricity+phone+water+gas;
    this.setState({
      output_electricity : electricity,
      output_phone : phone,
      output_water : water,
      output_gas : gas,
      annual_bill : homeTax,
      monthly_bill:total,
    });
    alert("Check below for more details")
 }



  render(){
    return(
      <div className="App">
        
        <header className="header">
          <h4>Domestic Utilities Bill Calculator</h4>
        </header>

        {/* ##### Block 1 - Input fields ##### */}
        <div className="App-body-1">
          <p>Enter the bill details:</p>
          <form className="to-do-form" onSubmit={this.calculateItem}>
            {/* 1st - Input -> Electricity */}
            <div className="inputdiv">
              <label className="inputlabels" htmlFor="electricity">Electricity</label>
              <input
                type="number"
                name="input_electricity"
                value={this.state.input_electricity}
                onChange={this.handleChange}
                placeholder="   kwh"
              />
            </div>

            {/* 2nd - Input -> Phone */}
            <div className="inputdiv">
              <label className="inputlabels" htmlFor="phone">Phone</label>
              <input
                type="number"
                name="input_phone"
                value={this.state.input_phone}
                onChange={this.handleChange}
                placeholder="   sec"
              />
            </div>

            {/* 3rd - Input -> Water */}
            <div className="inputdiv">
              <label className="inputlabels" htmlFor="water">Water</label>
              <input
                type="number"
                name="input_water"
                value={this.state.input_water}
                onChange={this.handleChange}
                placeholder="   litre"
              />
            </div>

            {/* 4th - Input -> Gas */}
            <div className="inputdiv">
              <label className="inputlabels" htmlFor="gas">Gas</label>
              <input
                type="number"
                name="input_gas"
                value={this.state.input_gas}
                onChange={this.handleChange}
                placeholder="   litre"
              />
            </div>


            {/* 5th - Input -> homeTax */}
            <div className="inputdiv">
              <label className="inputlabels" htmlFor="homeTax">Home Tax</label>
              <input
                type="number"
                name="input_homeTax"
                value={this.state.input_homeTax}
                onChange={this.handleChange}
                placeholder="   sq.ft"
              />
            </div>

            {/* submit Button -> calculateItem()  */}
            <button id="add-button" type="submit">Calculate</button>

          </form>
        </div>

        {/* Result Block */}
        <div className="bottom-body">
          <div id="header-2"><h4>Your Bill Details</h4></div>
          <div className="App-body-2">

            {/* Imported Component */}
            <Monthly
              i_electricity = {this.state.input_electricity}
              i_phone = {this.state.input_phone}
              i_water = {this.state.input_phone}
              i_gas = {this.state.input_gas}
              electricity = {this.state.output_electricity}
              phone = {this.state.output_phone}
              water = {this.state.output_water}
              gas = {this.state.output_gas}
              total={this.state.monthly_bill}
            />
            <Annual
              i_hometax={this.state.input_homeTax}
              annual_bill={this.state.annual_bill}  
            />

          </div>
        </div>


      </div>
    )
  }

}
export default App;
